import React, {Component, PropTypes} from 'react'

export default class QuestionnaireButton extends Component {
  static propTypes = {
    text: PropTypes.string,
  }

  state = {
    text: this.props.text || 'berprfen und Senden',
  }

  waitForSomeSecondsAndReset(text) {
    this.setState({text: 'Bitte warten ...'})
    setTimeout(() => {
      this.setState({text})
    }, 2000)
  }

  render() {
    return <button onClick={() => this.waitForSomeSecondsAndReset(this.state.text)}>{this.state.text}</button>
  }
}


